<template>
    <div class="bxYuyue">
      <ul class="typeList">
        <li @click="checknum= index; getList()" v-for="(item,index) in typelist" :key="index" :class="checknum==index?'check':''">
          {{item}}
          <span></span>
        </li>
      </ul>

      <MyLoadMore :list='goodList'  @is-loadmore = 'loadmore' :isLoad='isLoad'>
         <div class="goodList">
        <router-link tag="li" :to="{name:'yyXiangqin',params:{mark:item.mark,id:item.id}}" v-for="(item,index) in goodList" :key="index">
          <div class="glinfo">
            <div class="spimg">
              <img :src="item.img" alt="">
            </div>
            <div class="glbiao">
              <h3>{{item.ins_name}}</h3>
              <div class="jie">
                {{item.brief}}
              </div>
            </div>
          </div>
          <div class="yuyue">
            <span :class="item.process =='预约成功'?'yuscuss':item.process =='已见面'?'yijian':item.process =='已签约'?'qianyue':item.process =='已返款'?'fankua':item.process =='已取消'?'quxiao':''">

              {{item.process }}
            </span>
            <van-button type="default quyu" @click="cancelOrder(item.id)" v-if="item.process =='预约成功'||item.process =='已见面'">{{item.caozuo}}</van-button>
          </div>
        </router-link>
      </div>
      </MyLoadMore>
    </div>
</template>

<script>

import { apiGet } from '@/common/js/api';
import  MyLoadMore  from '@/common/MyLoadMore'
import { Dialog } from "vant"
    export default {
        name: "bx_yuyue",
      components:{ MyLoadMore },
      data(){
          return{
            page:1,
            isLoad:true,
            typelist:["全部","已预约","已见面","已签约","已返款"],
            goodList:[],
            checknum:0,
            accessToken:''
          }
      },
      mounted() {
        this.getList();
      },
      methods:{
        //获取预约列表
        loadmore(){
           apiGet("/user/inserve", {accessToken: this.accessToken, page: ++this.page, per: 10}).then(res => {
            if (res.status === 200) {
              this.goodList = this.goodList.concat(res.data.data.items);
              if(res.data.data.items.length< 10) {
                this.isLoad = false;
              } else {
                this.isLoad = true
              }
             }
           })

        },
        getList(){

          this.accessToken = this.$cookie.get('accessToken');
      /*    this.accessToken='9RVRcGJsjs7BzwvRTN5ZFgdQHleRj5z8';*/
          apiGet('/user/inserve', {accessToken: this.accessToken, page: this.page, per: 10,process:this.checknum}).then((res) => {
            if(res.status==200){
              this.goodList=res.data.data.items;
              console.log(res.data.data.items)
              console.log(this.goodList)
              this.isLoad=false;
            }
          }).catch(()=>{
            this.isLoad=false;
          })

        },
        //取消预约
        cancelOrder(id){
          Dialog.confirm({
            title: '警告',
            message: '是否确认取消预约？'
          }).then(() => {
            // on confirm
            apiGet("",{}).then(res=>{
              this.getList();
            })
          }).catch(() => {
            // on cancel
          });

        },
        //跳转预约详情
      /*  toDetail(id){
          this.$router.push({
            path:"/yyXiangqin",
            query:{
              id
            }
          })
        }*/
      },

    }
</script>

<style scoped lang="scss">
  .bxYuyue{
    background: #E6E9ED;
    height: 100%;
    overflow: auto;
    .typeList{
      width: 100%;
      color: #BCBCBC;
      z-index: 3;
    &::-webkit-scrollbar{display: none;};
    white-space: nowrap;
      height: 0.47rem;
      overflow: auto;
      position: fixed;
      top: 0;
      background: white;
      line-height: 0.47rem;
      li{
        width: 0.8rem;
        height: 100%;
        text-align: center;
        position: relative;
        display: inline-block;
        font-size: 0.17rem;
        span{position: absolute;bottom: 0}
      }
      .check{
        color: #3C7FEE;
        span{
          display: inline-block;
          background: #3C7FEE;
          width: 40%;
          height: .02rem;
          position: absolute;
          bottom: 0;
          left: 30%;
        }
      }
    }
    .goodList{
      width: 3.5rem;
      margin: 0 auto 0rem;
      padding-top: 0.6rem;
      li{
        width: 3.5rem;
        padding-top: 0.21rem;
        background: white;
        margin-bottom: 0.17rem;
        border-radius: 0.04rem;
        .glinfo{
          width: 3.2rem;
          margin: 0 auto;
          margin-bottom: 0.21rem;
          display: flex;
          justify-content: space-between;
          .spimg{
            width: 0.73rem;
            height: 0.73rem;
            border-radius: 0.04rem;
            img{
              width: 0.73rem;
              height: 0.73rem;
            }
          }
          .glbiao{
            width: 2.34rem;
            h3{font-size: 0.17rem;overflow: hidden;white-space: nowrap;text-overflow: ellipsis}
            .jie{
              color: #9A9A9A;
              font-size: 0.13rem;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
          }
        }
        .yuyue{
          height: 0.5rem;
          border-top: 1px solid #f5f5f5;
          line-height: 0.5rem;
          width: 100%;
          font-size: 0.15rem;
          display: flex;
          justify-content: space-between;
          padding: 0 0.13rem;
          span{
            width: 0.6rem;
            &.yuscuss{
              color: #E1A558;
            }
            &.yijian{
              color: #589CE1;
            }
            &.qianyue{
              color: #0BB3A5;
            }
            &.fankua{
              color: #C14C4C;
            }
            &.quxiao{
              color: #BCBCBC;
            }
          }
          .quyu{
            width: 1rem;
            height: 0.31rem;
            line-height: 0.31rem;
            text-align: center;
           margin: 0.1rem;
            color: white;
            font-size: 0.14rem;
            background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
          }
        }
      }
    }
  }
</style>



